
import { defineComponent } from 'vue';
import { store } from '@/components/store'

declare interface GraphicCell {
    index: number,
    value: number | ""
}

export default defineComponent({
    name: 'SudokuGrid',
    data() {
        return {
            store
        }
    },
    created() {
        for (let i = 0; i <= 80; ++i) {
            let newCell: GraphicCell = {
                index: i,
                value: ""
            }
            store.cells.push(newCell);
        }
    },
    methods: {
        checkInput(input: number | "") {
            //console.log(input);
            if (input === "") {
                return true;
            }
            if (input < 1 || input > 9) {
                return false;
            }
            return true;
        }
    }
});
